.wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.modal {
    display: flex;
    justify-content: space-between;
    width: 460px;
    padding: 32px;
    flex-direction: column;
    align-items: center;
    position: relative;
    gap: 24px;
    border-radius: 12px;
    background: var(--grey-0-white, #fff);
    box-shadow: 0px 8px 32px 0px rgba(0, 0, 0, 0.08);
}
.modal img {
    /* margin-top: 26px; */
    padding: 26px;
}
.formtext {
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    line-height: 26px;
}
.closeButton {
    border: none;
    cursor: pointer;
    position: absolute;
    width: 24px;
    height: 24px;
    right: 32px;
    top: 32px;
    border-radius: 100px;
    background: var(--black-alpha-alpha-4, rgba(0, 0, 0, 0.04));
}
.closeButton::before,
.closeButton::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    height: 2px;
    background-color: #b3b3b3;
    transform: translate(-50%, -50%);
}

.closeButton::before {
    transform: translate(-50%, -50%) rotate(45deg);
}

.closeButton::after {
    transform: translate(-50%, -50%) rotate(-45deg);
}
.buttonClose {
    align-self: end;
}

.left {
    color: var(--work-presets-text-white-mode-primary-g-800, #333);
    opacity: 1;
    align-self: start;
    /* text-align: left; */
}